<template>
  <!--eslint-disable-next-line-->
  <span v-for="content in book_content" class="content_show">{{ content }}</span>
  <div style="width: 100%;float: left;height: 10px;background-color: transparent"></div>
  <div class="book_option">
    <span class="book_option_span" @click="top_book">上一章</span>
    <span class="book_option_span" @click="next_book">下一章</span>
  </div>
</template>

<script>
import { ElMessage } from 'element-plus'

export default {
  name: 'book_content',
  data () {
    return {
      book_content: [],
      index: 1
    }
  },
  methods: {
    top_book () {
      if (this.index === 0) {
        ElMessage({
          message: '当前已是第一章',
          duration: 1000
        })
        return
      }
      this.index -= 1
      this.initial(this.$store.getters.GetBook.chapter_url[this.index])
    },
    next_book () {
      if (this.index === this.$store.getters.GetBook.chapter_url.length - 1) {
        ElMessage({
          message: '当前已是最后一章',
          duration: 1000
        })
        return
      }
      this.index += 1
      this.initial(this.$store.getters.GetBook.chapter_url[this.index])
    },
    initial (url) {
      this.api.initial.book_content(url).then(res => {
        this.book_content = res.data.data
      })
    }
  },
  created () {
    this.index = this.$route.params.index
    this.initial(this.$store.getters.GetBook.chapter_url[this.index])
  }
}
</script>

